* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #000;
  color: #e40037;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 25px;
}

ul {
  display: flex;
  gap: 50px;
  list-style-type: none;
}

.hero-image {
  height: 500px;
}
